<template>
  <div class="emergency-call-container">
    <!-- SOS按钮 -->
    <div class="sos-button-container">
      <div class="sos-button-outer" @click="sendEmergencyCall">
        <div class="sos-button-middle">
          <div class="sos-button-inner">
            <div class="sos-text">SOS</div>
          </div>
        </div>
      </div>
    </div>
    
    <!-- 提示文字 -->
    <div class="instruction-text">点击紧急呼救</div>
    
    <!-- 底部导航栏 -->
    <div class="bottom-nav">
      <div class="nav-item" @click="goToHomePage">
        <div class="nav-icon">
          <img src="../assets/home-icon1.png" alt="首页" class="nav-icon-img" />
        </div>
        <div class="nav-text">首页</div>
      </div>
      <div class="nav-item active" @click="goToMyPage">
        <div class="nav-icon">
          <img src="../assets/my-icon1.png" alt="我的" class="nav-icon-img" />
        </div>
        <div class="nav-text">我的</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'EmergencyCall',
  data() {
    return {
      fromReportView: false
    }
  },
  created() {
    // 获取来源信息
    this.fromReportView = this.$route.query.fromReportView === 'true';
  },
  methods: {
    goToHomePage() {
      // 根据来源返回相应的首页状态
      this.$router.push({
        path: '/home',
        query: { 
          showReportPage: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    goToMyPage() {
      // 返回我的页面，并保持状态
      this.$router.push({
        path: '/my-page',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    sendEmergencyCall() {
      // 跳转到紧急呼救成功页面
      this.$router.push({
        path: '/emergency-success',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    }
  }
}
</script>

<style scoped>
.emergency-call-container {
  position: relative;
  width: 393px;
  height: 852px;
  background-color: white;
  border-radius: 40px;
  overflow: hidden;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.sos-button-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
}

.sos-button-outer {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background-color: rgba(255, 99, 99, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
}

.sos-button-middle {
  width: 160px;
  height: 160px;
  border-radius: 50%;
  background-color: rgba(255, 71, 71, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
}

.sos-button-inner {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background-color: #FF4747;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.sos-text {
  font-size: 40px;
  font-weight: bold;
  color: white;
  letter-spacing: 2px;
}

.instruction-text {
  font-size: 24px;
  color: #333;
  margin-top: 20px;
}

.bottom-nav {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 70px;
  background-color: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border-top: 1px solid #eee;
}

.nav-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
}

.nav-icon {
  width: 24px;
  height: 24px;
  margin-bottom: 5px;
}

.nav-icon-img {
  width: 100%;
  height: 100%;
}

.nav-text {
  font-size: 12px;
  color: #999;
}

.nav-item.active .nav-text {
  color: #FF9500;
}
</style>